
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import enterInformation from './enterComponents/information'
import enterReports from './enterComponents/reports'
import axios from 'axios'
import loading from '@/components/loading'
export default {
name: 'enterprise',
components: {
loading,
Tab,
TabItem,
enterInformation,
enterReports
},
data () {
return {
isShow: false,
cou_index: 0
}
},
mounted () {

},
methods: {
getData () {
console.log('data')
axios.get('/api/sweep?batch=20181022135308').then(this.getDataSucc)
.catch(err => {
this.isShow = false
console.log(err)
})
},
getDataSucc (res) {

if (res && res.data) {
this.isShow = false
this.name = res.data.providers.p_name
this.response = res.data.providers
} else {
alert('')
}
},
onItemClick (index) {
this.cou_index = index
}
}
}



