import mapGrid from './mapGrid'
export default {
  name: 'grid',
  props: ['dataList', 'title', 'isMap', 'city'],
  components: {
    mapGrid
  },
  mounted () {

  },
  data () {
    return {
      listData: []
    }
  },
  watch: {
    'dataList' (val) {
      if (val.length !== 0) {
        this.listData.push(val)
      }
    }
  }
}



