
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

import axios from 'axios'
import loading from '@/components/loading'
import information from './homeComponents/information'
import reports from './homeComponents/reports'
import authentic from './homeComponents/authentic'
export default {
name: 'home',
data () {
return {
cou_index: 0,
isShow: false, // loading
city: '',
position: 'default',
showPositionValue: false,
toastText: ''
}
},
components: {
Tab,
TabItem,
Toast,
loading,
information,
reports,
authentic
},
mounted () {


this.init()
this.timer = setTimeout(() => {
this.showPosition('middle')
}, 1000)
},
methods: {
init () {
console.log(localStorage.getItem('getLocation'))
},
getData () {
console.log('data')
axios.get('/api/sweep?batch=20181022135308').then(this.getDataSucc)
.catch(err => {
this.isShow = false
console.log(err)
})
},
getDataSucc (res) {
console.log(res)
if (res && res.data) {
this.isShow = false
this.response = res.data.providers
} else {
alert('')
}
},
onItemClick (index) {
this.cou_index = index
},
showPosition (position) {
this.position = position
this.toastText = ''
this.showPositionValue = true
}
}
}



