import footerNav from './components/footerNav'
export default {
  name: 'App',
  components: {
    footerNav
  },
  data () {
    return {
      showNav: true
    }
  },
  methods: {
    routeChange () {
      let path = this.$route.path
      this.showNav = path !== '/'
    }
  },
  created () {
    this.routeChange()
  },
  watch: {
    // 
    '$route': function () {
      this.routeChange()
    }
  }
}



