import loading from '@/components/loading'
export default {
  name: 'location',
  data () {
    return {
      isShow: false, // loading
      city: ''
    }
  },
  components: {
    loading
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      console.log(localStorage.getItem('getLocation'))
      //    
      if (localStorage.getItem('getLocation') !== null) {
        this.openNext()
      }
    },
    getLocation () {
      /* eslint-disable */
      this.isShow = true// loading
      var self = this
      var geolocation = new BMap.Geolocation()
      geolocation.getCurrentPosition(function (r) {
        // 
        if (this.getStatus() === BMAP_STATUS_SUCCESS) {
            var myGeo = new BMap.Geocoder()
            myGeo.getLocation(new BMap.Point(r.point.lng, r.point.lat), function(result){
                if (result){
                    self.city = result.addressComponents.city
                    localStorage.setItem('getLocation', self.city)
                    self.openNext()
                } else {
                    self.openNext()
                }
            })
        }
      })
    },
    openNext () {
      // ajax callback(){
      this.isShow = false
      this.$router.push({ name: 'home', path: '/home'})
      // }
    }
  }
}



